<template>
  <div class="LoanAccounts">
      <div class="box_LoanAccount">
          <el-row>
            <el-col :span="3">
              <div class="grid-content bg-purple">
               <h3 class="fl"></h3>
              </div>
            </el-col>
            <el-col :span="3">
              <div class="grid-content bg-purple-light">
               <span class="fl" style='font-size: 12px;line-height: 36px;'></span>
              </div>
            </el-col>
            <el-col :span="3">
              <div class="grid-content bg-purple-light">
                
              </div>
            </el-col>
          </el-row>
          <el-row style='margin: 9px auto;'>
            <el-col :span="3">
              <div class="grid-content bg-purple">
               <h3 >{{datas.withDrawalMoney}}</h3>
              </div>
            </el-col>
            <el-col :span="3">
              <div class="grid-content bg-purple-light">
                <el-button size='mini' class='fl' @click='CashWithdrawalfn'></el-button>
              </div>
            </el-col>
            <el-col :span="3">
              <div class="grid-content bg-purple-light">
               {{datas.accountMoney}}
              </div>
            </el-col>
          </el-row>
          <span class="BankCardManagement"  @click='routers'></span>
      </div>
      <h5 class="fl" style="margin-bottom: 12px;margin-left: 13px;">11 </h5>
        <el-table
      :data="tableDatas"
      style="width: 100%">
      <el-table-column
        prop="date"
        label="">
      </el-table-column>
      <el-table-column
        prop="name"
        label="">
      </el-table-column>
       <el-table-column
        prop="name"
        label="">
      </el-table-column>
       <el-table-column
        prop="name"
        label="">
      </el-table-column>
       <el-table-column
        prop="name"
        label="">
      </el-table-column>
       <el-table-column
        prop="name"
        label="">
      </el-table-column>
       <el-table-column
        prop="name"
        label="">
      </el-table-column>
    </el-table>
    <h5 class="fl" style="margin: 13px;"></h5>
            <el-table
      :data="tableData"
      style="width: 100%;text-align: center;">
      <el-table-column
       style="text-align: center;"
        prop="createDate"
        label="">
      </el-table-column>
      <el-table-column
        prop="orderId"
        label="">
      </el-table-column>
       <el-table-column
        prop="accountType"
        :formatter="returnStatussas"
        label="">
      </el-table-column>
       <el-table-column
        prop="price"
        label="()">
      </el-table-column>
       <el-table-column
        prop="name"
        label="">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>

import { accountDetailList ,storeQueryAccountlnfo} from '@/api/LoanAccount'

export default {
  name: 'LoanAccount',
     data() {
      return {
          tableData:[],
          tableDatas:[],
          datas:{
            withDrawalMoney:0,
            accountMoney:0
          },
          returnStatussas : function (row, column, cellValue) {
            if (cellValue === 1){
                return '';
            }else if (cellValue === 2){
                return '';
            }else if (cellValue === 3){
                return '';
            }else if (cellValue === 4){
                return ' ';
            }
        },
      }
    },
     methods: {
        CashWithdrawalfn(){
            this.$router.push('CashWithdrawal')
        },
        routers(){
          this.$router.push('BankCardManagement')
        },

   	 },
    async mounted () {
         const response = await accountDetailList()
         const responseJson = await response.json();
         if(responseJson.statusCode == 200) {
          this.tableData=responseJson.object;
         }
          const responses = await storeQueryAccountlnfo()
         const responsesJson = await responses.json();
         if(responsesJson.statusCode == 200) {
          this.datas=responsesJson.object;
         }
  	},
      watch: {
        
      }
}
</script>

<style  scoped>
.box_LoanAccount{
  background-color: #fff;

padding: 20px;

margin: 20px auto;
position: relative;
}
.BankCardManagement{
  top: 10px;

right: 19px;

position: absolute;

font-size: 14px;
color: #337ab7 !important;
cursor: pointer;
}
</style>



